import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  cnpjFormatter,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, TomadorServico } from '../../../../models/dtos/mfp/tomadorServico';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const TomadorServicoSearchView: FC<SearchViewProps<TomadorServico>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'nome',
      text: 'Nome',
    },
    {
      dataField: 'cnpj',
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: cnpjFormatter,
      sort: true,
      text: 'C.N.P.J.',
    },
    {
      dataField: 'cei',
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'CEI',
    },
  ];

  return (
    <WindowComponent
      title="Manutenção de Tomador de Serviço / Obra de Construção"
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'TomadorServicoEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default TomadorServicoSearchView;
